#create-blog {
  margin: -60px -60px;
  display: flex;
  flex-direction: column;
}

#create-blog .wrapper {
  flex-direction: row;
  display: flex;
}

#create-blog .wrapper > div, #create-blog .wrapper textarea {
  height: 100vh;
  width: 50%;
  border: 1px solid #ccc;
  margin: 0px;
  padding: 20px;
}

#create-blog .meta {
  display: flex;
  flex-direction: row;
}

#create-blog input {
  /* width: 100%; */
}

#create-blog label {
  /* width: 70px; */
  font-size: 10px;
}

#create-blog .preview {
  overflow: scroll;
}