  color: #b0b0b0;
  font-weight: 400;
}

.discography .album ul li{
  list-style: none;
  line-height: 28px;
}


/*------------------------ 17 Responsive ------------------------*/

@media (max-width: 991px) {
  
  .wrapper.top_60{
    margin-top: 30px;
  }
  
  .code-skills {
    padding-top: 0;
  }
  
  #content {
    padding-bottom: 45px;
  }
  
  .clients .client{
    margin-bottom: 30px;
  }
  
  .events ul li .site-btn {
    float: none;
    margin-top: 15px;
  }
  
}

@media (max-width: 768px) {
  .portfolio-content .namenprice .blog-title{
    width: 100%;
  }
  
  .portfolio-content .namenprice .price{
    float: left !important;
    margin-top: 14px;
  } 
}

@media (max-width: 321px) {
  .timeline li .line-title {
    line-height: 20px;
  }
}

@media (min-width: 576px) {
  .container {
    max-width: 90%;
  }
}
