.vertical-timeline {
  width: 100%;
  margin-top: 0;
}

.vertical-timeline::before {
  background-color: #dddddd;
}

.vertical-timeline-element-icon i {
    font-size: 29px;
    display: block;
    width: 20px;
    height: 20px;
    position: relative;
    left: 50%;
    top: 50%;
    margin-left: -14px;
    margin-top: -14px;
}

.vertical-timeline-element-content p {
  font-size: .9rem;
}

h3.vertical-timeline-element-title {
  font-size: .9rem;
  padding-bottom: 1px;
}

h4.vertical-timeline-element-subtitle {
  font-size: .85rem;
}

.timeline-element-last .vertical-timeline-element-content {
  border-top: none;
}

@media only screen and (min-width: 1170px) {
  .vertical-timeline-element-content .vertical-timeline-element-date {
    left: 129%;
  }

  .vertical-timeline-element.vertical-timeline-element--right .vertical-timeline-element-content .vertical-timeline-element-date,
  .vertical-timeline-element:nth-child(even):not(.vertical-timeline-element--left) .vertical-timeline-element-content .vertical-timeline-element-date {
    right: 129%;
  }

  .vertical-timeline-element.vertical-timeline-element--right .vertical-timeline-element-content::before,
  .vertical-timeline-element:nth-child(even):not(.vertical-timeline-element--left) .vertical-timeline-element-content::before {
      border-right-color: #edf1f9b8; 
  }

  .vertical-timeline-element-content {
    background-color: #edf1f93d;
    width: 44%;
    border-radius: 0;
    border-top: 3px solid #bbc4ea;
  }

  .vertical-timeline-element-content::before {
    top: 24px;
    left: 100%;
    border-color: transparent;
    border-left-color: #edf1f9b8;
  }
}