.portfolio-item {
  padding-bottom: 30px;
  /* min-height: 400px; */
}

.portfolio-item .lead-icon {
  font-size: 60px;
  text-align: center;
  padding-bottom: 10px;
  border-bottom: 1px solid #e2e1e1;
  color: #8f9abd;
}

.portfolio-item .default-icon {
  color: #b3bbd2;
}

.portfolio-item .title {
  font-size: 16px;
  padding-bottom: 5px;
  color: #000;
  font-weight: 600;
}

.portfolio-item figcaption {
  font-size: 16px;
  color: #989898;
  line-height: 18px;
  display: block;
  padding: 15px 5px;
}

.portfolio-item figure {
  margin: 0;
}

.portfolio-item .category {
  margin-bottom: 8px;
  font-size: .8rem;
  text-transform: uppercase;
}

.portfolio-item .date {
  margin-top: 10px;
  font-weight: 900;
  color: #aaa;
  font-size: .7rem;
}

@media(max-width:991px) {
  .portfolio-item figcaption {
    text-align: center;
  }

  .portfolio-item .lead-icon {
    font-size: 85px;
  }
}