nav ul {
  list-style: none;
}

header nav{
  background: #fff;
  display: inline-block;
  border-radius: 6px;
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
  height: 70px;
  width: 100%;
  margin-bottom: 30px;
  line-height: 70px;
  box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
  -webkit-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
  -moz-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
}

header nav ul li{
  float: left;
}

header nav ul li a{
  font-size: 14px;
  font-weight: bold;
  color: #000;
  padding: 0 10px;
  line-height: 73px;
  display: block;
}

header nav ul li a:hover{
  transition: all ease 0.3s;
  -webkit-transition: all ease 0.3s;
  -moz-transition: all ease 0.3s;
  color: var(--main-highlight-color);
  text-decoration: none;
}

header nav ul li a:after{
  border-right: 1px solid #dfdfdf;
}

header nav a.home-btn{
  color: #fff;
  border-radius: 6px 0 0 6px;
  -webkit-border-radius: 6px 0 0 6px;
  -moz-border-radius: 6px 0 0 6px;
  padding: 0px 24px;
  float: left;
  height: 70px;
  line-height: 70px;
  margin-right: 10px;
  transition: all ease 0.4s;
  line-height: 77px;
  background-color: var(--main-highlight-color);
}

header nav a.is-active {
  color: var(--main-highlight-color);
}

header nav a.home-btn:hover{
  transition: all ease 0.3s;
  -webkit-transition: all ease 0.3s;
  -moz-transition: all ease 0.3s;
  color: white;
  background-color: var(--main-secondary-color) !important;
}

header nav a.home-btn i{
  font-size: 21px;
}

header nav .site-btn{
  margin: 18px 18px 0 5px;
}

.social-icons a {
  color: #000;
  font-size: 12px;
  transition: all ease 0.2s;
  width: 24px;
  height: 18px;
  display: inline-block;
  padding-right:30px;
}


.social-icons a:hover {
  font-size: 14px;
}

.fb:hover{
  color: #3b5998!important;
  transition: all ease 0.2s;
}

.tw:hover{
  color: #55acee!important;
  transition: all ease 0.2s;
}

.ins:hover{
  color: #cd486b!important;
  transition: all ease 0.2s;
}

.bh:hover{
  color: #1769ff!important;
  transition: all ease 0.2s;
}

.dr:hover{
  color: #ea4c89!important;
  transition: all ease 0.2s;
}

header nav .menuout{
  z-index: 10
}

header nav .menuout{
  display: none;
}

header nav ul.tabs {
  padding-left: 0;
}

.hamburger {
  padding: 0 15px;
}

#hamburger-menu {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.1s linear;
}

@media (max-width: 768px) {
  header {
    margin-top: 30px;
  }
  
  header nav ul{
  }
  
  header nav ul.tabs li:first-child, header nav .menuout ul.tabs li {
    display: block;
  }
  
  header nav ul.tabs li {
    display: none;
  }
  
  header nav .menuout{
    width: 100%;
    position: relative;
  }
  
  header nav .menuout .menuin{
    width: 100%;
    background: #fff;
    position: absolute;
    z-index: 4;
    margin-top: 15px;
    border-radius: 8px;
    -moz-border-radius: 8px;
    -webkit-border-radius: 8px;
    box-shadow: 0px 2px 92px 10px rgba(0, 0, 0, 0.05);
    -webkit-box-shadow: 0px 2px 92px 10px rgba(0, 0, 0, 0.05);
    -moz-box-shadow: 0px 2px 92px 10px rgba(0, 0, 0, 0.05);
  }
  
  header nav .menuout .menuin ul.tabs li:first-child{
    display: none;
  }
  
  header nav .menuout .menuin ul.tabs li{
    float: none;
    text-align: center;
    border-bottom: solid 1px #efefef;
  }
  
  header nav .menuout .menuin ul.tabs li a{
    width: 100%;
    display: block;
    cursor: pointer;
    line-height: 58px;
  }
  
  header nav .hamburger{
    font-size: 20px;
    margin-right: 10px;
    cursor: pointer;
    line-height: 71px;
    padding-left: 21px;
    border: 0;
    border-left: solid 1px #eaeaea;
  }
  
  header nav .menuout{
    display: block;
  }
  
  .social-icons{
    margin-right: 14px;
    line-height: 67px;
  }
}

@media screen and (max-width: 991px) and (min-width: 769px) {
  header nav{
    margin-top: 30px;
  }
  
  nav .social-icons{
    display: none;
  }
}

@media (max-width: 321px) {
  .social-icons{
    display: none;
  }
  
  header nav .hamburger{
    border: 0;
  }
}