.services .service .service-title{
  font-size: 14px;
  font-weight: 500;
  line-height: 30px;
  padding-bottom: 5px;
}

.service .icon {
  text-align: center;
}

.service .icon img {
  padding-bottom: 10px;
  width: 77%;
}

.service {
  background: #fff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0px 0px 48px 0px rgba(4, 6, 4, 0.08);
  min-height: 230px;
  margin-bottom: 20px;
}

.service .icon {
  padding-left: 0;
  padding-bottom: 10px;
}

.service .icon i {
  font-size: 40px;
}

@media (max-width: 991px) {
  .services .service{
    margin-bottom: 30px;
  }
}