:not(pre) > code[class*="language-"], pre[class*="language-"] {
    background: #fbfaf9;
}

.article-title{
  font-size: 26px;
  font-weight: bold;
  line-height: 30px;
}

#article {
  padding: 0 15px;
}

#article #content {
  margin: 15px;
}

#article .post .post-content p {
  margin-top: 2em;
  line-height: 32px;
  font-size: 21px;
}

#article .post .post-content h3 {
  margin-top: 2em;
  font-weight: 600;
}

#article .post .article-image {
  width: 100%;
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  overflow: hidden;
}

#article .post .article-image img{
  width: 100%;
}

#article .post .article-iframe iframe{
  width: 100%;
}

#article .post{
  padding: 20px 50px;
}

#article .post .post-date {
  color: #999999;
}

#article nav {
  margin-bottom: 10px;
}