.profile{
  background: #fff;
  border-radius: 6px;
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
  -o-border-radius: 6px;
  width: 100%;
  display: inline-block;
  box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
  -webkit-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
  -moz-box-shadow: 0px 2px 92px 0px rgba(0, 0, 0, 0.07);
}

.profile-name{
  line-height: 18px;
  padding: 20px;
  margin-bottom: 10px;
}

.profile .name{
  text-transform: uppercase;
  font-size: 17px;
  font-weight: bold;
}

.profile .job{
  font-size: 13px;
  color: #777777;
}

.profile-image {
  margin-top: -18px;
  -webkit-clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
  clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
}

.profile-image img{
  width: 100%;
}

ul.profile-information{
  list-style: none;
  /* margin: 8px 0px 8px 35px; */
  padding-left: 25px;
}

ul.profile-information li{
  border-left: solid 2px #eee;
  padding: 0 0 8px 20px;
  font-size: 14px;
  color: #787878;
}

ul.profile-information li p{
  font-size: 13.5px;
  color: #7F7F7F;
  line-height: 12px;
}

ul.profile-information li:last-child{
  border: 0;
  margin-left: 2px;
  margin-top: -2px;
}

ul.profile-information li:before{
  content: "";
  border: solid 2px;
  border-color: var(--main-highlight-color);
  width: 8px;
  height: 8px;
  border-radius: 50px;
  -moz-border-radius: 50px;
  -webkit-border-radius: 50px;
  font-size: 8px;
  margin-left: -25px;
  margin-top: 2px;
  font-weight: 400;
  background: #ffffff;
  display: block;
  position: absolute;
}

ul.profile-information li span{
  color: #000;
}


ul.profile-information li:first-child{
  padding-bottom: 10px;
  padding-left: 30px;
  position: relative;
}

ul.profile-information li:first-child img {
  margin: -44px -44px;
  display: inline-block;
  position: absolute;
  width: 25px;
}

ul.profile-information li:first-child:before{
  /*content: '\f2c0';
  font-family: FontAwesome;
  font-size: 13.5px;*/
  background-color: var(--main-highlight-color);
  margin-left: -51px;
  margin-top: -30px;
  margin-bottom: 20px;
  position: relative;
  border: 0;
  width: 40px;
  height: 40px;
  padding: 11px 14px;
  box-shadow: 0px 2px 32px 0px rgba(4, 123, 248, 0.3);
}

ul.profile-information li:first-child:after{
  content: '';
  width: 30px;
  height: 10px;
  position: absolute;
  top: 0;
  left: 0;
  margin: 16px -15px;
  background-size: 27px 10px;
}

.profile-icon {
  position: absolute;
  top: 12px;
  font-size: 15px;
  left: -8px;
  color: white;
}

@media (max-width: 991px) {
  figure.profile-image {
    clip-path: none;
    padding-top: 30px;
  }

  ul.profile-information li:first-child {
    padding-bottom: 10px;
    padding-left: 30px;
    visibility: hidden;
  }
  
  .download-cv {
    text-align: center;
  }
  
  ul.profile-information {
    list-style: none;
    margin: 0 auto 30px;
    display: table;
  }
  
  .profile{
    padding-bottom: 30px;
  }
}

@media screen and (max-width: 991px) and (min-width: 576px) {
  
  .profile .name {
    font-size: 20px;
    margin-top: 11px;
  }
  
  .profile-image {
    margin-top: -18px;
    -webkit-clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
    clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
    margin-top: -40px;
  }
}

