.site-btn {
  margin: 5px 20px;
  font-size: 14px;
  font-weight: 500;
  border: 0;
  border-radius: 50px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  padding: 1px 29px;
  line-height: 35px;
  background-color: var(--main-highlight-color);
}

a.site-btn, button.site-btn {
  color: white;
}

.site-btn.black{
  background: #000 !important;
}

.site-btn:hover{
  color: white;
  transition: all ease 0.3s;
  background-color: var(--main-secondary-color) !important;
  text-decoration: none;
}

.site-btn.icon{
  text-align: left;
  padding: 1px 6px 1px 19px;
}

.site-btn.icon i{
  color: #000;
  background: #fff;
  margin-left: 15px;
  border-radius: 50px;
  font-size: 11px;
  height: 27px;
  width: 27px;
  float: right;
  text-align: center;
  line-height: 29px;
  margin-top: 4px;
}
