footer {
  background: #1e1e1e;
  min-height: 70px;
  color: #fff;
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  line-height: 72px;
  display: flex;
  width: 100%;
  margin: 15px;
}

footer .name{
  color: #fff;
  font-size: 15px;
}

footer .copyright{
  color: #9c9c9c;
  font-size: 12px;
  text-align: right;
}

footer .copyright a{
  color: #fff;
}

footer .copyright a:hover{
  color: #dfdfdf;
  transition: all ease 0.3s;
}

@media (max-width: 1199px) {
  footer .footer .copyright{
    text-align: center;
  }
}

@media (max-width: 991px) {
  footer .footer .copyright{
    text-align: center;
  }
}

@media (max-width: 321px) {
  footer .footer {
    line-height: 19px;
    padding-top: 14px;
  }
}